<template>
	<div class="order-list-container">
		<topNav class="topNav" :title="title" :isleftarrow="isleftarrow"></topNav>
		<van-tabs v-model="active" swipeable @click="clickTabs">
			<van-tab v-for="(item,index) in tabs" :key="index" :title="item.name" />
		</van-tabs>
		<van-list v-model="loading" :finished="finished" :finished-text="finishedTxt" @load="onListLoad">
			<div class="list-item" v-if="list" v-for="item in list" :key="item.id">
				<van-cell center>
					<template #icon>
						<van-icon style="font-size: 16px;margin-right: 10px;" name="shop-o" />
					</template>
					<template #title>
						{{item.shopName}}
					</template>
					<template #default>
						{{item.statusStr}}
					</template>
					 
				</van-cell>
				<van-row class="group-inner" @click="toOrderDetail(item.id)">
					<van-card :num="item2.number" :price="item2.amountSingle" :desc="item2.property" :title="item2.goodsName" :thumb="item2.pic"
					 v-for="(item2,index2) in goodsMap[item.id]" :key="item2.id">
					</van-card>
				</van-row>
				<div class="footer">
					<van-button plain round size="small" @click="toOrderDetail(item.id)">查看详情</van-button>
					<van-button v-if="item.status == 2" type="danger" plain round size="small" @click="comfirmReceipt(item.id)">确认收货</van-button>
					<van-button v-if="item.status == 3" type="info" plain round size="small">发表评论</van-button>
				</div>
			</div>
		</van-list>
	</div>
</template>

<script>
	import util from '@/common/util/util'
	import topNav from '../../components/topNav.vue'
	export default {
		data() {
			return {
				tabs: [{
						name: '全部',
						status: ''
					},
					{
						name: '待付款',
						status: '0'
					},
					{
						name: '待发货',
						status: '1'
					},
					{
						name: '待收货',
						status: '2'
					},
					{
						name: '待评价',
						status: '3'
					}
				],
				active: 0,
				goodsMap: {},
				goodsId: [],
				mList: [],
				list: [],
				page: 1,
				pageSize: 10,
				loading: false,
				finished: false,
				finishedTxt: '没有更多了',
				title: '',
				isleftarrow: '',
			}
		},
		components: {
			topNav
		},
		created() {
			const status = this.$route.query.status === undefined ? '' : this.$route.query.status;
			this.active = this.tabs.findIndex(item => item.status === status);
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
		},
		methods: {
			toOrderDetail(id) {
				this.$router.push({
					path: '/order-detail',
					query: {
						id
					}
				})
			},
			onListLoad() {
				// console.log('onListLoad');
				// console.log(this.tabs[this.active].status,this.page);
				this.getOrderList(this.tabs[this.active].status, this.page++, this.pageSize)
			},
			getOrderList(status = '', page = this.page, pageSize = this.pageSize, isTab = false) {
				const params = {
					token: util.storage.get('token'),
					status,
					page,
					pageSize
				}
				this.$request.post('/order/list', params).then(res => {
					console.log('订单查询', res);
					if (res.code === 404) {
						this.loading = false;
						this.finished = true;
						this.finishedTxt = page > 1 ? '没有更多了' : '您还没有任何订单哦~';
						if (isTab && page === 1) {
							this.list = [];
							this.goodsMap = {};
						}
						return;
					}
					if (res.code !== 0) {
						// if(res.code === 700){
						// 	this.list = [];
						// }
						this.$toast('没有更多的信息');
						this.loading = false;
						this.finished = true;
						return;
					}
					if (isTab) {
						this.list = res.data.orderList;
						this.goodsMap = res.data.goodsMap;
						this.loading = false;
						this.finishedTxt = '没有更多了';
						return;
					}
					const list = res.data.orderList
					list.forEach(item => {
						this.goodsMap[item.id] = res.data.goodsMap[item.id];
					});
					for (const key in this.goodsMap) {
						this.goodsMap[key].forEach(e => e.amountSingle = parseFloat(e.amountSingle).toFixed(2))
					}
					this.list = this.list.concat(list);
					console.log(list)
					this.loading = false;
					// this.finished = true;
					this.finishedTxt = '没有更多了';
					// console.log('list',this.list);
				});
			},
			clickTabs: util.debounce(function(index) {
				// console.log('clickTabs');
				this.loading = true;
				this.finished = false;
				this.getOrderList(this.tabs[index].status, 1, this.pageSize, true);
			}, 1000),
			comfirmReceipt(id) {
				let orderId = id;
				let token = util.storage.get('token');
				this.$dialog.confirm({
					title:'提示',
					message:'确认收货？\n<span style="color:red">提前收货造成损失，本商城将不会为您提供后续申述服务。</span>'
				}).then(()=>{
					// alert('确认收货')
					this.$request.post('/order/delivery',{
						orderId,token
					}).then(res=>{
						// console.log(res);
						if(res.code != 0 ){
							return;
						}
						// this.$toast(res.msg);
						this.$router.go(0);
					})
				}).catch(()=>{
					
				});
			},
		}
	}
</script>

<style lang="less" scoped>
	.list-item {
		text-align: start;
		background-color: #fff;
		border-radius: 10px;
		overflow: hidden;
		margin: 20px 10px;
		// box-shadow: 0px 0px 10px 2px rgba(0,0,0,.1);

		.van-card {
			background-color: #fff;

			// border-top: 1px solid rgba(0,0,0,0.1);
			// margin-top: 5px;
			.van-card__title {
				font-size: 14px;
			}
		}

		.footer {
			display: flex;
			justify-content: flex-end;
			align-items: center;
			padding: 10px 0px;

			.van-button--default{
				border: 1px solid #999;
			}
			.van-button{
				margin-right: 10px;
			}
		}
	}
</style>
